import React from 'react';

const About = () => {
    return (
        <div className='wrapper'>
            <div className='about'>
            <div className='skills'>
            <div className='frameworks'>
              <h4>Languages &amp; Frameworks</h4>
              <div className="languages-frameworks">
                <p className="text-center">HTML, CSS, JavaScript, React, Bootstrap</p>
              </div>
                <p className="icon"><i class="fab fa-html5" aria-hidden="true"></i><i class="fab fa-css3-alt" aria-hidden="true"></i><i class="fab fa-js-square" aria-hidden="true"></i><i className="fab fa-react" aria-hidden="true"></i><i className="fab fa-bootstrap" aria-hidden="true"></i></p>
            </div>
            <div className="tools">
              <h4 classN="">Tools &amp; Design</h4>
              <div classN="tools-design">
              <p className="text-center">Visual Studio Code, Github, Figma </p>
              </div>
              <p className="icons d-flex justify-content-evenly fs-3"><i className="fas fa-laptop-code" aria-hidden="true"></i><i className="fab fa-github" aria-hidden="true"></i><i className="fab fa-figma" aria-hidden="true"></i></p>
            </div>
               </div>
                <video className="blob" muted loop src="https://cdn.dribbble.com/users/693064/screenshots/15394896/media/4de0d9b22e8f21c9b3b45573d9d88b82.mp4">
               </video>
        </div>
        </div>
    )
}

export default About
